*,
*::before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: var(--font-family);
  position: relative;
  background-color: var(--bg-color);
  color: var(--light-color);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.hero-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-color);
}

@media screen and (min-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }
}

@media (min-width: 992px) {
  .hero-title {
    font-size: 3rem;
  }
}

.hero-ranking {
  color: #fddd5b;
  margin: 1em 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  user-select: none;
}

.hero-ranking span {
  margin: 0.5rem 1rem 0.5rem 0;
  flex: 0 0 auto;
}

.hero-ranking__comments a {
  color: var(--second-color);
}

.hero-ranking__comments a:hover {
  color: var(--second-color);
}

.hero-ranking__level {
  padding: 0.5em 1em;
  border: 1px solid var(--second-color);
  text-transform: uppercase;
  border-radius: 0.5em;
  color: var(--text-color);
  font-weight: 500;
}

.hero-description {
  margin: 1rem 0 1.5rem;
  color: var(--text-color);
  line-height: 1.5;
}

.hero-invite {
  margin-bottom: 2rem;
}

.hero-invite p {
  font-size: 0.875em;
  color: var(--text-color);
  opacity: 0.8;
  margin: 0;
}

.hero-invite input {
  max-width: 300px;
  width: 100%;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  outline: none;
  border: 1px solid var(--border-color);
  color: var(--text-color);
  margin: 1rem 0;
  background-color: var(--bg-color);
  font-size: 1em;
  line-height: 1.5;
}

.hero-invite input::placeholder {
  color: var(--text-color);
  opacity: 0.7;
}

.hero-invite input:focus {
  border-radius: 0.5rem;
  outline: none;
  border: 1px solid var(--second-color);
  -webkit-box-shadow: 0px 0px 5px 2px #33b1ff;
  -moz-box-shadow: 0px 0px 5px 2px #33b1ff;
  box-shadow: 0px 0px 5px 2px #33b1ff;
}

@media (min-width: 768px) {
  .hero-invite input {
    margin-right: 1em;
  }
}

.hero-invite div {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.hero-invite__btn {
  height: 42px;
  background-color: var(--accent-color);
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  transition: all 0.3s ease-in-out;
  line-height: 1.6;
}

.hero-invite__btn:hover {
  text-decoration: none;
  opacity: 0.9;
}

.hero-terms {
  font-size: 0.875rem;
  opacity: 0.8;
  color: var(--text-color);
}

.hero-terms a {
  color: var(--second-color);
}

.hero-image {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -10;
  max-height: 100vh;
  min-height: 100vh;
  width: 100%;
  object-fit: cover;
  background-position: top;
  display: block;
  filter: brightness(0.2);
}

@media (min-width: 1024px) {
  .hero-image {
    position: initial;
    min-height: 100vh;
    filter: none;
  }
}

.hero-teacher {
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 1em;
  text-align: end;
  display: none;
}

@media (min-width: 1024px) {
  .hero-teacher {
    display: block;
  }
}

.hero-teacher a {
  text-decoration: none;
  color: var(--light-color);
}

.hero-teacher__name, .hero-teacher__rol {
  font-weight: 700;
  margin: 0;
}

.hero-teacher__rol {
  font-weight: 500;
}

@media (min-width: 1024px) {
  .position {
    position: relative;
  }
}

.progress-container {
  display: flex;
  justify-content: center;
  background-color: var(--bg-color);
  filter: brightness(0.9);
  padding: 0 1em;
}

.progress {
  width: 100%;
  max-width: 600px;
  display: flex;
  justify-content: space-between;
  margin: 1em 0;
}

.progress-dot {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--text-color);
  position: relative;
  flex: 1 0;
}

.progress-dot__circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 3px solid var(--border-color);
  position: relative;
}

.progress-dot.is-active .progress-dot__circle {
  border-color: var(--accent-color);
}

.progress-dot.is-active.is-complete .progress-dot__circle {
  background-color: var(--accent-color);
}

.progress-dot.is-active.is-complete .progress-dot__circle:before {
  content: "✓";
  color: var(--bg-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -53%);
}

.progress-dot__name {
  margin: 1em 0;
  display: none;
}

@media (min-width: 768px) {
  .progress-dot__name {
    display: block;
  }
}

.progress-dot:before {
  content: "";
  width: 75%;
  height: 2px;
  background-color: var(--border-color);
  position: absolute;
  z-index: 100;
  right: 100%;
  top: 50%;
  transform: translate(50%, -50%);
}

@media (min-width: 768px) {
  .progress-dot:before {
    top: 10px;
  }
}

.progress-dot.is-active:before {
  background-color: var(--accent-color);
}

.progress-dot:first-child:before {
  display: none;
}

.step-icon {
  display: flex;
  align-content: center;
  align-items: center;
  padding: 1.5em 0;
  border-bottom: 1px solid var(--border-color);
}

.step-icon > span {
  width: 48px;
  height: 48px;
  background-color: var(--accent-color);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1.5em;
}

.step-icon > span i.fa-user {
  font-size: 1.5em;
}

.step-icon .step-head__title {
  color: var(--text-color);
  font-size: 1.5em;
  margin-bottom: 0;
}

.step-icon .step-head__subtitle {
  font-size: 0.875em;
  line-height: 2.5;
  margin: 0;
  opacity: 0.8;
  color: var(--text-color);
}

.step-icon .step-head__subtitle span {
  font-weight: 700;
}

input {
  width: 100%;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  outline: none;
  border: 1px solid var(--border-color);
  color: var(--text-color);
  margin: 1rem 0;
  background-color: var(--bg-color);
  font-size: 1em;
  line-height: 1.5;
}

input::placeholder {
  color: var(--text-color);
  opacity: 0.7;
}

input:focus {
  border-radius: 0.5rem;
  outline: none;
  border: 1px solid var(--second-color);
  -webkit-box-shadow: 0px 0px 5px 2px #33b1ff;
  -moz-box-shadow: 0px 0px 5px 2px #33b1ff;
  box-shadow: 0px 0px 5px 2px #33b1ff;
}

.step-input .form-control {
  margin: 1.5em 0 0;
}

.step-input__title {
  color: var(--text-color);
  font-size: 1em;
  font-weight: 500;
  margin: 0.5em 0;
}

.step-input__info {
  color: var(--text-color);
  font-size: 0.875em;
  line-height: 2.5;
  margin: 0;
  opacity: 0.8;
}

.step-input__inputs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1em;
}

.step-input .radio {
  display: flex;
}

.step-input__radio {
  width: 92px;
  height: 48px;
  border: 1px solid var(--border-color);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1em;
  border-radius: 0.5em;
}

.step-input__radio.step2 {
  flex: 1 0;
}

.step-input__radio input[type="radio"] {
  width: 24px;
  height: 24px;
  border-color: var(--bg-color);
  margin-right: 1em;
  border: none;
  outline: none;
}

.step-input__radio input[type="radio"]:checked {
  background: var(--second-color);
  display: inline-block;
  padding: 0 0 0 0px;
  border: none;
  outline: none;
}

.form-control.show {
  margin: 2em 0;
  padding: 1.5em 0;
  background-color: var(--bg-color);
  filter: brightness(0.9);
}

.step-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5em;
  margin-bottom: 2em;
  color: var(--text-color);
}

.step-footer__question {
  display: flex;
  align-items: center;
}

.step-footer__question p {
  opacity: 0.8;
  margin-right: 1em;
}

.step-footer__question a {
  color: var(--second-color);
  transition: all 0.8s ease-in-out;
}

.step-footer__btn {
  padding: 0.875em 2em;
  font-size: 1em;
  color: var(--light-color);
  background-color: var(--accent-color);
  text-transform: uppercase;
  border-radius: 0.5em;
  transition: all 0.3s ease-in-out;
  font-weight: 500;
  letter-spacing: 1px;
}

.step-footer__btn:hover {
  text-decoration: none;
  filter: brightness(0.9);
}

:root {
  --font-family: "Roboto", sans-serif;
}

:root {
  --first-color: #001c59;
  --second-color: #33b1ff;
  --accent-color: #df284a;
  --dark-color: #282d31;
  --dark-color-alt: #141618;
  --light-color: #fafdff;
  --text-color: var(--light-color);
  --bg-color: var(--first-color);
  --border-color: #40587c;
}
